  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

ion-card {
  background: rgba(255, 255, 255, 0.164);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  /*background: rgb(28,138,242);
  background: linear-gradient(0deg, rgba(28,138,242,1) 47%, rgba(45,166,253,1) 47%); //     #fdfdfd            #14A8CD*/
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 100%;
  height: 100%;
  border-radius: 0.25rem;
  padding: 1rem;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.15);
  border-radius: 10px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  border: 0.5px solid rgba(0, 0, 0, 0.082);
}
ion-card .contImg {
  width: 100%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
ion-card .contImg img {
  width: 80%;
  height: auto;
}
ion-card .text {
  width: 100%;
  height: 40%;
  color: black;
  font-size: 15px;
  font-weight: bold;
}
ion-card .text ion-text {
  width: 100%;
  font-size: 20vw;
  text-align: center;
  color: rgb(0, 0, 0);
}

.textillo {
  font-size: 20px;
  margin-bottom: 10px;
  color: black;
  font-weight: bold;
}

.form-group {
  margin-left: 100px;
}

.selector-color {
  display: inline-block;
  margin: 0px 2px;
  width: auto;
  height: auto;
  position: relative;
  border-radius: 3px;
  cursor: pointer;
  overflow: hidden;
  transform: rotate(0deg);
  transition: 1s;
}

.texto-descripcion {
  color: white;
  font-size: 1.3em;
}

.selected.selector-color {
  border: solid black 1px;
}

.selector-color input {
  width: auto;
  background-size: cover;
  position: absolute;
  height: auto;
  top: 5px;
  left: 5px;
  box-shadow: inset rgba(0, 0, 0, 0.5) 0px 0px 12px;
  transition: 1s;
}

.selected.selector-color input {
  width: auto;
  height: auto;
  top: 0px;
  left: 0px;
}

.contiene-descripcion > table > tbody > tr > td > div > img {
  max-width: 100%;
  height: auto;
